import React, { useLayoutEffect, useState } from 'react';

import * as s from './style.module.scss';

const browsers = {
  kakao: '카카오톡',
  line: '라인',
  other: '인 앱',
};

function Warning() {
  const [browser, setBrowser] = useState<'kakao' | 'line' | 'other'>('other');

  useLayoutEffect(() => {
    if (navigator.userAgent.match(/KAKAOTALK/i)) {
      setBrowser('kakao');
    } else if (navigator.userAgent.match(/Line/i)) {
      setBrowser('line');
    } else {
      setBrowser('other');
    }
  }, []);

  return (
    <aside className={s.warning}>
      {browsers[browser]} 브라우저에서는 웹사이트가 정상적으로 보이지 않을 수
      있으니 사파리나 크롬으로 봐주세요.
    </aside>
  );
}

export default Warning;
