import React, { useEffect, useMemo, useRef, useState } from 'react';

import * as s from './style.module.scss';

function Main() {
  const [width, setWidth] = useState<number>(0);

  const ref = useRef<HTMLElement>(null);
  const observer = useRef<ResizeObserver | null>(null);

  useEffect(() => {
    observer.current = new ResizeObserver(([entry]) => {
      setWidth(entry.contentRect.width);
    });

    observer.current.observe(ref.current!);

    return () => {
      observer.current?.disconnect();
    };
  }, []);

  // TODO(dwchung): Add reveal effect.

  return useMemo(
    () => (
      <main className={s.main} ref={ref}>
        <div className={s.wrap}>
          <img
            className={s.icon}
            alt="Wedding Rings"
            src="data:image/png;base64,"
          />
          <p>
            같은 나라와 공간에서 몇번이나 스쳐 지나갔던{' '}
            <span className={s.nowrap}>인연으로 시작해</span>
            {width >= 640 ? <br /> : ' '}
            2020년 비로소 사랑으로{' '}
            <span className={s.nowrap}>맺어진 저희가,</span>
            {width >= 640 ? <br /> : ' '}
            2023년 새해의 길목에서 하나가{' '}
            <span className={s.nowrap}>되려 합니다.</span>
          </p>

          <p>
            이렇게 인생에서 가장 중요하고{' '}
            <span className={s.nowrap}>소중한 날,</span>
            {width >= 640 ? <br /> : ' '}
            저희를 아껴주신 감사한 분들을 모시는 것이 도리인 줄{' '}
            <span className={s.nowrap}>알고 있지만</span>
            {width >= 640 ? <br /> : ' '}
            코로나로 인한 사회적 분위기와 두 사람의{' '}
            <span className={s.nowrap}>뜻에 따라</span>
            {width >= 640 ? <br /> : ' '}
            양가 부모님 및 형제자매만 모시고 작은 결혼식을{' '}
            <span className={s.nowrap}>하게 되었습니다.</span>
          </p>

          <p>
            즐거운 행사에 함께 모시지 못하는 점{' '}
            <span className={s.nowrap}>송구스럽게 생각하며,</span>
            {width >= 640 ? <br /> : ' '}
            진심어린 마음으로 축하해{' '}
            <span className={s.nowrap}>주셨으면 좋겠습니다.</span>
          </p>

          <p>
            행복하고 예쁘게 <span className={s.nowrap}>잘 살겠습니다.</span>
          </p>
        </div>
      </main>
    ),
    [width],
  );
}

export default Main;
